import axios from "axios";
import { alegra_api } from "../config";
import { formatDate } from "../utils";



export async function getSellers() {
    return (await axios.get(alegra_api + '/sellers')).data;
}

export async function createInvoice(quantity,seller) {
    let date = Date.now();
    return (await axios.post(alegra_api + '/invoices', {
        headers: { Authorization: middleware_auth }, body: JSON.stringify(
            {
                date: formatDate(date),
                dueDate: formatDate(date + 1000*3600*24*8),// 8 d√≠as
                client: 1,
                items: [
                    {
                        "id": 1,
                        "price": 1,
                        quantity
                    }
                ],
                seller
            }
        )
    }));
}